.news-card {
    background: var(--background-alt);
    border-radius: var(--rounded);
    padding: 1rem;
    box-shadow: var(--shadow);
    margin-bottom: 1rem;
    transition: var(--transition);
  }
  .news-card:hover {
    transform: translateY(-3px);
  }
  .news-title {
    font-weight: var(--bold);
    font-size: 1.1rem;
  }
  .news-date {
    font-size: 0.9rem;
    color: var(--gray);
  }
  
  .news-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    gap: 0.25rem;
  }
  
  .news-description {
    margin-top: 0.5rem;
    font-size: 1rem;
    color: var(--dark-gray);
    line-height: 1.5;
  }
  
  /* Responsive Design for Mobile */
  @media (max-width: 600px) {
    .news-header {
      flex-direction: column;
      align-items: flex-start;
    }
  
    .news-date {
      margin-top: 0.25rem;
    }
  }